.App {
	text-align: center;
}

.App-logo {
	animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
	pointer-events: none;
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.homepage-banner {
	position: relative;
	height: 100vh;
	background-color: #282c34;
	width: 100%;
	margin: 0 auto;
}

.background-image {
	opacity: 0.4;
	height: 93vh;
	width: 100%;
	position: absolute;
	display: block;
	background-image: url("./assets/videos/shutterstock_691408807.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	opacity: 0.2;
}
